<template>
  <div class="maps" style="width: 100%; height: 100%">

      <GmapMap :center="{lat: 21.4389, lng: -158.0001}" :zoom="11" style="width: 100%; height: 100%"> 
          <Traffic-Layer></Traffic-Layer>
          <Heat-Map :data="{lat: parseFloat(markers[0].lat),lng: parseFloat(markers[0].long)}"></Heat-Map>
          <gmap-marker
            :key="index"
            v-for="(items, index) in markers"
            :position="{lat: parseFloat(items.lat), lng: parseFloat(items.long)}"
            @click="center={lat: parseFloat(items.lat), lng: parseFloat(items.long)}"
          ></gmap-marker>

      </GmapMap>

  </div>
</template>

<script>
import {database,auth} from '@/fb.js'
import Traffic from '@/assets/map/TrafficLayer.js'
import HeatMap from '@/assets/map/HeatMapLayer.js'
export default {
  name: 'MapsPage',
  components: {
      'Traffic-Layer': Traffic,
      'Heat-Layer': HeatMap
  },
  data () {
      return {
          markers: []
      }
  },
  created() {
      database.collection('Pins').onSnapshot(data => {

          let temp = [];
          data.forEach(function(doc) {
                temp.push(doc.data());
            });
          this.markers = temp;
          console.log(data);

      });
  },
  methods: {
      
  }
}
</script>