<template>
  
  <v-app class="grey lighten-4">
    
    <v-content>
      <Nav-Bar v-on:emittedEvent="changeComponent($event)" :signedIn="signedIn" :curComp="currentComponent" />
        <component :is="currentComponent" :signedIn="signedIn"> </component>
    </v-content>
  
  </v-app>

</template>

<script>
import {bus} from './main.js'
import {database,auth} from './fb.js'
import HomePage from './components/HomePage'
import NavBar from './components/NavBar'

import Report from './components/Report'
import Forums from './components/Forums'
import Team from './components/Team'

import SignStuff from './components/SignStuff'

import Maps from './components/Maps'
import Camera from './components/Cameras'

export default {
  name: 'App',
  components: {
    'Home-Page': HomePage,
    'Nav-Bar': NavBar,
    'Report-Page': Report,
    'Maps-Page': Maps,
    'Forums-Page': Forums,
    'Team-Page': Team,
    'Camera-Page': Camera
  },
  created() {
    auth.onAuthStateChanged(user => { 
        if(user) {
            this.signedIn = true;
            // console.log('User Logged In ',user);
        } else {
            this.signedIn = false;
            // console.log('User is logged out');
        }
    });

    bus.$on("categoryChange", data => {
      this.currentComponent = data;
    });

   bus.$on("forumChange", data => {
      this.currentComponent = data;
    });
  },
  data () {
    return {
      currentComponent: 'Home-Page',
      signedIn: false
    }
  },
  methods: {
    changeComponent: function(event) {
      this.currentComponent = event;
    }
  }
}
</script>